<template>
  <v-row justify="center">
    <v-dialog
      v-model="value"
      persistent
      max-width="290"
    >
      <v-card>
        <v-card-title class="text-h5">
          {{title || "Confirm?"}}
        </v-card-title>
        <v-card-text v-if="text">{{text}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="secondary darken-1"
            text
            @click="cancel"
          >
            {{cancelText || "cancel"}}
          </v-btn>
          <v-btn
            color="primary darken-1"
            text
            @click="accept"
          >
            {{okText || "ok"}}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
    props: {
       title:String,
       text:String,
       okText:String,
       cancelText:String,
       value:Boolean,
    },
    methods:{
        accept(){
            this.$emit('input',false);
            this.$emit('accept',true);
        },
        cancel(){
            this.$emit('input',false);
            // this.$emit('cancel');
        }
    }
}
</script>
