import { goal } from '@/common/config';
import db from '@/common/services/firebaseUserService';
import store from '@/store';
import { doc, updateDoc } from 'firebase/firestore';


export async function createSelection(word) {
    console.log({ store });
    if (store.state.user.currentRace) {
        store.state.user.currentRace.forEach(race => {
            if (race.word === word) {
                throw (new Error('Word already exists'));
            }
        });
    }
    const user = doc(db, '/users', '' + store.state.common.currentUser.id);
    // console.log('user: ' + commonStore.state().currentUser.id)
    // let len = testPicts.length - 1;
    // let start = Math.floor(Math.random() * len);
    await updateDoc(user, {
        race: [...store.state.user.currentRace,
        { word, pictures: {} }]
    });
}


export async function closeSelection(selectionIndex, selected) {
    console.log({ selected, selectionIndex });
    const user = doc(db, '/users', '' + store.state.common.currentUser.id);


    let newRaceInfo = store.state.user.currentRace;


    newRaceInfo[selectionIndex].selected = selected;

    let sellers = store.state.common.currentUser.sellers;

    Object.entries(newRaceInfo[selectionIndex].pictures).forEach(e => {
        sellers[e[0]].proposals -= 1;
    });
    // newRaceInfo[selectionIndex].pictures = [];//remove database trash


    sellers[selected.user].score += 3;
    // sellers[selected.user].proposals -= 1;

    let raceDone = null;
    if (sellers[selected.user].score >= goal) {//race done, we have a winner!!
        raceDone = {
            sellers, winner: selected.user, timestamp: Date.now()
        };
        setTimeout(() =>
            newRaceInfo.forEach(async sel => {
                try {
                    let auxSeller = doc(db, '/users', sel.user);
                    await updateDoc(auxSeller, { raceDone });
                } catch (e) {
                    console.error('Failed to notify end of race to ' + sel.user);
                }
            }));//sellers notification is not critical since the will detect if the race is over anyway
    }

    console.log({ newRaceInfo, sellers });

    let usrData = {
        race: newRaceInfo, sellers
    };
    if (raceDone) {
        usrData.raceDone = raceDone;
    }

    await updateDoc(user, usrData);
}

export default db;